import { Component, OnInit, style } from '@angular/core';
import {BasicService} from '../service/Basic.service';
import { TagContentType } from '@angular/compiler';
import {ModalComponent} from '../modal/modal.component';

@Component({
  selector: 'app-ring',
  templateUrl: './ring.component.html',
  styleUrls: ['./ring.component.scss']
})
export class RingComponent implements OnInit {
  name:string;  
  data:any;

  constructor(private MyService : BasicService) {
    this.name = "Angular Service";
    //this.MyService.GetPosts().subscribe(posts => { console.log(posts)}); 
    this.MyService.GetPosts().subscribe(posts =>{ this.data = posts }); 
  }


  ngOnInit() {
  }

}



