import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';
import { NavComponent } from './components/nav/nav.component';
import { GallryComponent } from './components/gallry/gallry.component';
import { BannerComponent } from './components/banner/banner.component';
import { AboutComponent } from './components/about/about.component';
import { ContactComponent } from './components/contact/contact.component';
import {RouterModule,Routes } from '@angular/router';
import { FooterComponent } from './components/footer/footer.component';
import { RingComponent } from './components/ring/ring.component';
import { HttpModule } from '@angular/http';
import { BasicService } from './components/service/Basic.service';
import { ModalComponent } from './components/modal/modal.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms'

const appRoutes: Routes = [
  { path:'home', component:GallryComponent},
  { path:'about', component:AboutComponent},
  { path:'contact', component:ContactComponent},
  { path:'ring', component:RingComponent},
  { path: '**', redirectTo: 'home', pathMatch: 'full' }
]

@NgModule({
  declarations: [
    AppComponent,
    NavComponent,
    GallryComponent,
    BannerComponent,
    AboutComponent,
    ContactComponent,
    FooterComponent,
    RingComponent,
    ModalComponent,
    
  ],
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forRoot(appRoutes),
    
  ],
  providers: [BasicService],
  bootstrap: [AppComponent]
})
export class AppModule { }



